body {
	background-color: #fff;
	padding-top: 20px;
	margin: 0;
}
li {
	font-family: sans-serif;
}
ul.header li {
	display: inline;
	list-style-type: none;
	margin: 0;
}
ul.header {
	background-color: #111;
	padding: 0;
}
ul.header li a {
	color: #fff;
	font-weight: bold;
	text-decoration: none;
	padding: 20px;
	display: inline-block;
}
.content {
	background-color: #f9f9f9;
	padding: 20px;
	border-radius: 20px;
}
.content h2 {
	padding: 0;
	margin: 0;
}
.content li {
	margin-bottom: 10px;
}
.active {
	background-color: #64b5f6;
}
.home .active {
	border-radius: 20px;
}
div {
	font-family: "Trebuchet MS";
}
#nameheader {
	font-family: "Helvetica Neue";
	font-size: 64px;
	margin-left: 15px;
}
.iconHeader {
	display: flex;
	flex-direction: row;
	align-items: center;
}
.square {
	color: #fff;
	border-radius: 30px;
	padding: 20px;
	margin-bottom: 40px;
	box-shadow: 0 0 15px -5px rgba(0, 0, 0, 0.5);
	height: auto;
}
.square h3 {
	font-size: 28px;
}
.square p {
	font-size: 18px;
}
#about p {
	font-size: 21px;
}
.iconHeader h3 {
	margin: 0;
}
.iconHeader {
	margin-top: 20px;
}
.row {
	display: flex;
	flex-direction: row;
}
#projects {
	width: 100%;
	margin-right: 20px;
}
.optify {
	background-color: #48c9b0;
	border-radius: 25px;
	height: 50px;
	width: 50px;
}
#contact {
	width: 40%;
	margin-left: 20px;
}
#football {
	max-width: 80%;
	height: auto;
	margin: auto;
	border-radius: 40px;
	box-shadow: 0 0 15px -5px rgba(0, 0, 0, 0.5);
}
